/* eslint-disable no-unused-vars */
import React from "react";
import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
const SET_URL_PHOTO = "SET_URL_PHOTO";
const DELETE_POST = "DELETE_POST";

let initialState = {
  posts: [
    {
      id: 1,
      title: "One-page site for tourism with fullscren swiper",
      link: "https://offerta.portfolio-example.website/sm-home.html",
      photo:
        "data:image/jpeg;base64,",
      textButton: "Html, scss, js",
      likeCount: 0,
    },

    {
      id: 2,
      title: "Charity Fund to help children cancer (8 pages)",
      link: "https://offerta.portfolio-example.website/timoshas.html",
      photo:
        "data:image/jpeg;base64,",
      textButton: "Html, scss, js",
      likeCount: 20,
    },
    {
      id: 3,
      title: "One-page site for a construction company",
      link: "https://offerta.portfolio-example.website/realstate.html",
      photo:
        "data:image/jpeg;base64,",
      textButton: "Html, scss, js",
      likeCount: 30,
    },
    {
      id: 4,
      title: "Parallax forest",
      link: "https://offerta.portfolio-example.website/parallax-forest.html",
      photo:
        "data:image/jpeg;base64,",
      textButton: "Html, scss, js",
      likeCount: 5,
    },
    {
      id: 5,
      title: "The site of musical instruments",
      link: "https://offerta.portfolio-example.website/gitar.html",
      photo:
        "data:image/jpeg;base64,",
      textButton: "Html, scss, js",
      likeCount: 45,
    },

    // { id: 2, post: "It's my first post", likeCounter: 23 },
    // { id: 3, post: "Yo", likeCounter: 105 },
    // { id: 4, post: "Yo", likeCounter: 105 },
  ],
  profile: null,
  status: "",
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      return {
        ...state,
        posts: [
          ...state.posts,
          {
            id: 6,
            title: action.newPostBody,
            link: "#",
            photo:
              "data:image/jpeg;base64,",
            textButton: "React",
            likeCount: 0,
          },
        ],
      };
    }

    case SET_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    case SET_URL_PHOTO: {
      return {
        ...state.profilePage.profile.data.photos,
        large: action.data,
      };
    }
    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      };
    }
    case DELETE_POST: {
      return {
        ...state,
        posts: state.posts.filter((p) => p.id !== action.postId),
      };
    }

    default:
      return state;
  }
};

export const addPostActionCreator = (newPostBody) => ({
  type: ADD_POST,
  newPostBody,
});

export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
});
export const getUserProfile = (userId) => (dispatch) => {
  usersAPI.getUserProfile(userId).then((data) => {
    dispatch(setUserProfile(data));
  });
};
export const setUserStatus = (status) => ({
  type: SET_STATUS,
  status,
});
export const deletePost = (postId) => ({
  type: DELETE_POST,
  postId,
});
export const setUserPhoto = (data) => ({
  type: SET_URL_PHOTO,
  data,
});
export const getUserStatus = (userId) => (dispatch) => {
  profileAPI.getUserStatus(userId).then((response) => {
    dispatch(setUserStatus(response.data));
  });
};
export const updateStatus = (status) => (dispatch) => {
  profileAPI.updateStatus(status).then((response) => {
    if (response.data.resultCode === 0) {
      dispatch(setUserStatus(status));
    }
  });
};
export const updateUrlPhoto = (photos) => (dispatch) => {
  profileAPI.updatePhoto(photos).then((response) => {
    if (response.data.resultCode === 0) {
      dispatch(setUserPhoto(photos));
    }
  });
};

export default profileReducer;
